.edit-leave-container{
    display: flex;
    justify-content: center;
    height: 100vh;
}

.edit-leave-container .header{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.edit-leave-container .header .menu a{
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: bold;
    background: var(--header-color);
    padding: 4px 8px;
    border-radius: 4px;
    color: #fff;
    margin: 2px;
}

.edit-leave-container .header p{
    font-size: 8rem;
    font-family: var(--source-sans-font);
}

.edit-leave-container .header p span{
    color: var(--header-color);
}

.edit-leave-container .leave-form{
    width: 50%;
    background: var(--asphalt-color);
}


.edit-leave-container .leave-form{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.edit-leave-container .leave-form .edit-leave-form{
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 50vh;
    padding: 20px;
    background: #ffffff;
    border-radius: 7px;
    
}

.edit-leave-container .leave-form .edit-leave-form label{
    font-size: 2rem;
}

.edit-leave-container .leave-form .edit-leave-form .edit-leave-header{
    font-size: 4rem;
    text-align: center;
    font-family: var(--ubuntu-font);
}

.edit-leave-container .leave-form .edit-leave-btn{
    margin-top: 10px;
    font-size: 2rem;
    border: none;
    border-radius: 5px;
    background-color: var(--asphalt-color);
    color: #fff;
    padding: 4px;
    cursor: pointer;
}
.react-date-picker__inputGroup{
    font-size: 2rem;
}


@media (max-width:768px){
    .edit-leave-container .leave-form .edit-leave-form .edit-leave-header{
        font-size: 2rem;
    }
    .edit-leave-container .header p{
        font-size: 6rem;
        font-family: var(--source-sans-font);
    }
    .edit-leave-container .leave-form .edit-leave-form label, input{
        font-size: 2rem;
    }
    .edit-leave-container .leave-form .edit-leave-form{
        width: 70%;
        height: 45vh;
    }
    .edit-leave-container .leave-form .edit-leave-btn{
        font-size: 1rem;
    }
    .react-date-picker__inputGroup{
        font-size: 3rem;
    }

}

@media (max-width:500px){
    .edit-leave-container .leave-form .edit-leave-form label, input{
        font-size: 4rem;
    }
    .edit-leave-container{
        flex-direction: column;
        width: 100vw;
    }
    .edit-leave-container .leave-form{
        width: 100vw;
        background: #fff;
    }
    .edit-leave-container .leave-form .edit-leave-header{
        display: none
    }
    .edit-leave-container .header{
        padding-top: 100px;
        width: 100%;
    }
    .edit-leave-container .header .menu a{
        font-size: 2.5rem;
    }
    .edit-leave-container .leave-form .edit-leave-form{
        width: 90%;
        height: 35vh;
    }
    .edit-leave-container .leave-form .edit-leave-btn{
        font-size: 4rem;
    }
    .react-date-picker__inputGroup{
        font-size: 4rem;
    }
    .edit-leave-container .header p{
        font-size: 9rem;
    }
    .edit-leave-container .header .menu a{
        font-size: 2.5rem;
    }

}