.nav-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-container ul{
    display: flex;
}

.nav-container ul a{
    text-decoration: none;
}

.nav-container ul li{
    list-style: none;
    padding: 10px;
    font-size: 1.8rem;
    cursor: pointer;
    color: var(--asphalt-color);
    font-family: var(--ubuntu-font);
}

@media (max-width: 768px){
    .nav-container ul li{
        font-size: 3rem;
    }
}

@media (max-width: 500px){
    .nav-container ul li{
        font-size: 5rem;
    }
}